:root {

  /*### Primary*/
  --blue: hsl(246, 80%, 60%);
  --light-red-work: hsl(15, 100%, 70%);
  --soft-blue-play: hsl(195, 74%, 62%);
  --light-red-study: hsl(348, 100%, 68%);
  --lime-green-exercise: hsl(145, 58%, 55%);
  --violet-social: hsl(264, 64%, 52%);
  --soft-orange-selfcare: hsl(43, 84%, 65%);

  /* ### Neutral */

  --very-dark-blue: hsl(226, 43%, 10%);
  --dark-blue: hsl(235, 46%, 20%);
  --desaturated-blue: hsl(235, 45%, 61%);
  --pale-blue: hsl(236, 100%, 87%);
}

/* Box sizing rules */
*,
*::before,
*::after {
  box-sizing: border-box;
}


@font-face {
  font-family: "Rubik";
  src: url("../fonts/Rubik-VariableFont_wght.ttf");
}

@font-face {
  font-family: "Rubik";
  src: url("../fonts/Rubik-Italic-VariableFont_wght.ttf");
  font-style: italic;
}

body {
  height: 100dvh;
  display: flex;
  align-items: center;
  justify-content: center;
  font-family: 'Rubik', sans-serif;
  font-weight: 300;
  font-size: 18px;
  background-color: black;
  color: white;
}

.container{
  width: 1440px;
  height: 1024px;
  display: grid;
  place-content: center;
  background-color: var(--very-dark-blue);
}

.dashboard{
  width: 1120px;
  height: 520px;
  display: grid;
  grid-template-columns: 256px 256px 256px 256px;
  grid-template-rows: 244px 244px;
  gap: 32px;
}

.dashboard-user{
  grid-column: 1;
  grid-row: 1/3;
}

.dashboard-user-box{
  background-color: var(--blue);
  position: absolute;
  width: 256px;
  height: 354px;
  border-radius: 16px;
  z-index: 1;
  padding: 34px;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
}

.dashboard-user-box img {
  width: 84px;
  border: 3px solid white;
  border-radius: 50%;
  margin-bottom: 40px;
}

.dashboard-user-box-title{
  font-size: 15px;
  color: var(--pale-blue);
  margin-bottom: 2px;
}

.dashboard-user-box-name{
  font-size: 42px;
}

.dashboard-user-timer{
  position: relative;
  top: 320px;
  width: 256px;
  height: 200px;
  border-radius: 0px 0px 16px 16px;
  padding: 32px 40px;
  background-color: var(--dark-blue);
  z-index: 0;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: center;
}

.timer {
  font-size: 16px;
  margin-top: 22px;
  color:var(--pale-blue);
  background-color: var(--dark-blue);
  border: none;
}

.timer:hover {
  cursor: pointer;
  color: white;
}

.active {
  color: white;
}

#id01{
  grid-column: 2;
  grid-row: 1;
  & .dashboard-stats-background{
    background-color: var(--light-red-work);
  }
}

#id02{
  grid-column: 3;
  grid-row: 1;
  & .dashboard-stats-background{
    background-color: var(--soft-blue-play);
  }
}

#id03{
  grid-column: 4;
  grid-row: 1;
  & .dashboard-stats-background{
    background-color: var(--light-red-study);
  }
}

#id04{
  grid-column: 2;
  grid-row: 2;
  & .dashboard-stats-background{
    background-color: var(--lime-green-exercise);
  }
}

#id05{
  grid-column: 3;
  grid-row: 2;
  & .dashboard-stats-background{
    background-color: var(--violet-social);
  }
}

#id06{
  grid-column: 4;
  grid-row: 2;
  & .dashboard-stats-background{
    background-color: var(--soft-orange-selfcare);
  }
}

.dashboard-stats-background{
  position: absolute;
  width: 256px;
  height: 61px;
  border-radius: 16px 16px 0px 0px;
  z-index: 0;
  display: grid;
  place-content: center end;
  overflow: hidden;
}

.dashboard-stats-background img {
  position: relative;
  padding: 0px 24px;
}

.dashboard-stats-content{
  position: relative;
  top:45px;
  left: 0px;
  width: 256px;
  height: 199px;
  border-radius: 16px;
  padding: 29px;
  background-color: var(--dark-blue);
  z-index: 1;
  display: flex;
  flex-direction: column;
  align-items: stretch;
  justify-content: flex-start;
  }

.dashboard-stats-content-head{
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 24px;
  & .stats-content-head-title{
    font-weight: 500;
  }
}

.stats-content-head-dots {
  width: 18px;

}

.stats-content-head-dots:hover{
  cursor: pointer;
  filter: invert(00%) sepia(100%) saturate(100%) hue-rotate(180deg) brightness(100%) contrast(100%);
}

.dashboard-stats-content-body{
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
}

.stats-content-body-current{
  font-size: 56px;
  font-weight: 300;
  margin-bottom:8px;
}

.stats-content-body-previous{
  font-size: 15px;
  color: var(--pale-blue)
}

.dashboard-stats-content-body:hover{
  filter: brightness(1.4);
  cursor: pointer;
}

.attribution {
  position:absolute;
  justify-self: center;
  font-size: 11px;
  text-align: center;
  bottom: calc(50dvh - 520px);
}
.attribution a {
  color: hsl(228, 45%, 44%);
}

@media only screen and (max-width: 1140px){

  body{
    height: auto;
  }
  
  .container{
    width: 375px;
    height: 1469px;
  }
  
  .dashboard{
    width: 330px;
    height: auto;
    display: grid;
    grid-template-columns: 330px;
    grid-template-rows: 200px 160px 160px 160px  160px 160px 160px;
    gap: 26px;

  }
  
  .dashboard-user{
    grid-column: 1;
    grid-row: 1;
  }

  .dashboard-user-box{
    position: absolute;
    width: 330px;
    height: 134px;
    border-radius: 16px;
    z-index: 1;
    padding: 28px;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: flex-start;
  }

  .dashboard-user-box img {
    width: 72px;
    border: 3px solid white;
    border-radius: 50%;
    margin-bottom: 0px;
  }

  .dashboard-user-box div {
    margin-left: 16px;
  }
  
  .dashboard-user-box-title{
    font-size: 14px;
  }
  
  .dashboard-user-box-name{
    font-size: 22px;
  }
  
  .dashboard-user-timer{
    position: relative;
    top: 120px;
    width: 330px;
    height: 80px;
    border-radius: 0px 0px 16px 16px;
    padding: 22px 28px;
    background-color: var(--dark-blue);
    z-index: 0;
    display: flex;
    flex-direction: row;
    align-items: flex-end;
    justify-content: space-between;
  }
  
  .timer {
    font-size: 16px;
    margin-top: 22px;
    color:var(--pale-blue);
    background-color: var(--dark-blue);
    border: none;
  }

  #id01{
    grid-column: 1;
    grid-row: 2;
  }
  
  #id02{
    grid-column: 1;
    grid-row: 3;
  }
  
  #id03{
    grid-column: 1;
    grid-row: 4;
  }
  
  #id04{
    grid-column: 1;
    grid-row: 5;
  }
  
  #id05{
    grid-column: 1;
    grid-row: 6;
  }
  
  #id06{
    grid-column: 1;
    grid-row: 7;
  }

  .dashboard-stats-background{
    position: absolute;
    width: 330px;
    height: 61px;
    border-radius: 16px 16px 0px 0px;
    z-index: 0;
    display: grid;
    place-content: center end;
    overflow: hidden;
  }
  
  .dashboard-stats-content{
    top:38px;
    width: 330px;
    height: 120px;
    padding: 29px;
    }
  
  .dashboard-stats-content-head{
    margin-bottom: 6px;
  }
  
  .dashboard-stats-content-body{
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
  }
  
  .stats-content-body-current{
    font-size: 28px;
    margin-bottom:0px;
  }
  
  .stats-content-body-previous{
    font-size: 15px;
  }

  .attribution {
    bottom: -215px;
  }

}